@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");


* {
  box-sizing: border-box;
}

/* GLOBAL STYLES */


:root{
  --primary-bg: rgb(0, 15, 20);
  --primary-title: rgb(233, 196, 106);
  --small-note: rgb(233, 178, 128);
  --color-light: rgb(41, 140, 227);
  --color-hover: rgb(7, 0, 24);
  --transition: 400ms ease;
}
::-webkit-scrollbar {
  display: none;
}

body,
.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
  background: var(--primary-bg);

}

button {
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  color: rgb(8, 127, 140);
}
button:hover {
  color: var(--color-light);
}

.App {
  display: flex;
}

/* SIDEBAR STYLES */

.app-sidebar {
  width: 30%;
  height: 100vh;
  border-right: 1px solid #ddd;
}

.app-sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 25px;
}
.app-sidebar-header h1 {
  margin: 0;
  color: #fff;
}

.app-sidebar-notes {
  height: calc(100vh - 78px);
  overflow-y: scroll;
}
.app-sidebar-note {
  padding: 25px;
  cursor: pointer;
}
.sidebar-note-title {
  display: flex;
  justify-content: space-between;
  color: var(--primary-title);
}
.app-sidebar-note button {
  color: crimson; 
}

.app-sidebar-note button:hover{
  color: rgb(255, 0, 51)
}

.app-sidebar-note p {
  margin: 10px 0;
}
.app-sidebar-note small {
  display: block;
  /* color: #999; */
  color: var(--small-note);
}

/* .app-sidebar-note:hover {
  background: var(--color-hover);
  border-radius: 0 1rem 1rem 0;
} */

.app-sidebar-note.active{
  border-radius: 0 1rem 1rem 0;
}

.app-sidebar-note.active,
.app-sidebar-note.active small {
  background: var(--color-hover);
  color: white;
}

.app-sidebar-note.false{
  position: relative;
  z-index: 1000;
  transition: var(--transition);
}

.app-sidebar-note.false::before{
  content: '';
  width: 0%;
  height: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -99999;
  border-radius: 0 1rem 1rem 0;
  background: var(--color-hover);
  transition: 400ms ease;
}

.app-sidebar-note.false:hover::before{
  width: 100%;
}



/* NOTE EDITOR/PREVIEW (MAIN) STYLES */

.app-main {
  width: 70%;
  height: 100vh;
}
.app-main-note-edit,
.app-main-note-preview {
  height: 50vh;
}

.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #fff;
}

/* Editing */
.app-main-note-edit {
  padding: 25px;
}

.app-main-note-edit input,
textarea {
  display: block;
  border: 1px solid var(--color-light);
  margin-bottom: 20px;
  width: 100%;
  height: calc(50vh - 130px);
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
  background: var(--primary-bg);
  border-radius: 0.5rem;
  color: var(--primary-title);
}
.app-main-note-edit input {
  height: 50px;
  font-size: 2rem;
}

/* Preview */
.app-main-note-preview {
  border-top: 1px solid #ddd;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.02);
}

.preview-title {
  padding: 25px 25px 0 25px;
  margin: 0;
  color: var(--primary-title);
}

.markdown-preview {
  padding: 0 25px 25px 25px;
  font-size: 1rem;
  line-height: 2rem;
  color: var(--primary-title);
}